import { Component, h, Host, Prop } from '@stencil/core';
import { PARAGRAPH_APPEARANCES, ParagraphAppearance, PARAGRAPH_SIZES, ParagraphSize } from './models/paragraph.types';

@Component({
  tag: 'wink-paragraph',
  styleUrls: {
    telenet: 'styles/brands/paragraph.telenet.scss',
    base: 'styles/brands/paragraph.base.scss',
  },
  shadow: true,
})
export class Paragraph {
  @Prop() appearance: ParagraphAppearance = PARAGRAPH_APPEARANCES.body;
  @Prop() size: ParagraphSize = PARAGRAPH_SIZES.regular;

  render() {
    return (
      <Host class={`font--${this.appearance}-${this.size}`}>
        <slot />
      </Host>
    );
  }
}
